
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListSubheader from '@material-ui/core/ListSubheader';
import Avatar from '@material-ui/core/Avatar';
import Link from '@material-ui/core/Link';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import StarIcon from '@material-ui/icons/Star';
import Divider from '@material-ui/core/Divider';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import CheckIcon from '@material-ui/icons/Check';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    backgroundColor: '#ffffff',

  },

  listHeader:{
    textAlign: 'center',
    fontSize: '1.8rem',
    color: '#000000',
    fontWeight: 300,
    fontFamily: 'Inter, sans-serif',
    padding: theme.spacing(2, 0, 1),

  },
  headerHero:{
    fontWeight: 800,
    fontSize:'6.8rem',
    fontFamily: 'IBM Plex Sans, sans-serif',
    color:'#000',
  },
  subHeaderHero:{
    fontWeight: 100,
    fontSize:'4.1rem',
    fontFamily: 'Lobster, cursive',
    color:'#000',
  },
  container:{
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(8, 0, 4),
  },
  paperFrame:{
    border: '0px solid',
    boxShadow:'0 8px 30px rgba(0,0,0,0.18)',
    borderRadius:'5px',
    p:0,
    lineHeight:0,
  },
}));

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

const HeroSection = () => {
  const classes = useStyles();

  return(
    <Box className={classes.root}>
      <Container maxWidth="md" >
        <Container className={classes.container}>
          <Typography
            align="center"
            color="text.primary"
            variant="h1"
             className={classes.headerHero}

          >
            <strong>JustShapes</strong>
          </Typography>
          <Typography gutterBottom
            align="center"
            color="text.secondary"
            variant="h1"
            className={classes.subHeaderHero}>
            Forever Charity
          </Typography>
        </Container>
      <Grid container spacing={6}>
        <Grid item xs={12} md={6}>
           <List>
              <Paper className={classes.paperFrame}>
              <Typography className={classes.listHeader}>
                The Art
              </Typography>

                <ListItem className={classes.root}>
                  <ListItemIcon>
                      <CheckIcon color="primary" />
                  </ListItemIcon>
                  <ListItemText primary="Emotionally pleasing generative art" secondary="" />
                </ListItem>
                <ListItem>
                <ListItemIcon>
                    <CheckIcon color="primary" />
                </ListItemIcon>
                  <ListItemText primary="Seamless 10 second looping videos" secondary="" />
                </ListItem>
                <ListItem>
                <ListItemIcon>
                    <CheckIcon color="primary" />
                </ListItemIcon>
                  <ListItemText primary="Depth in collectible and unquie attributes" secondary="" />
                </ListItem>
                <ListItemLink href="#simple-list" alignItems="center"  href="#art">
                  <ExpandMoreIcon color="primary" alignItems="center" style={{width:'100%', textAlign:'center',}}  />
                </ListItemLink>
              </Paper>
            </List>

          </Grid>
          <Grid item xs={12} md={6}>
             <List>
              <Paper className={classes.paperFrame}>
              <Typography className={classes.listHeader}>
                The Charity
              </Typography>



                <ListItem className={classes.root}>
                  <ListItemIcon>
                      <StarIcon color="primary" />
                  </ListItemIcon>
                  <ListItemText primary="Donate toys to children monthly, forever" secondary="" />
                </ListItem>
                <ListItem>
                <ListItemIcon>
                    <StarIcon color="primary" />
                </ListItemIcon>
                  <ListItemText primary="Assets grow via Solana ecosystem, forever" secondary="" />
                </ListItem>
                <ListItem>
                <ListItemIcon>
                    <StarIcon color="primary" />
                </ListItemIcon>
                  <ListItemText primary="100% of royalties go to the charity, forever" secondary="" />
                </ListItem>
                <ListItemLink href="#simple-list" alignItems="center"  href="#charity">
                  <ExpandMoreIcon color="primary" alignItems="center" style={{width:'100%', textAlign:'center',}}  />
                </ListItemLink>
              </Paper>
            </List>
          </Grid>
        </Grid>
        <br /><br />


        </Container>
    </Box>
  );
}

export default HeroSection;
